<script lang="ts">
  import { run } from 'svelte/legacy';

  import { onMount } from 'svelte';
  import { io, type Socket } from "socket.io-client";
  import type { MatchupEntry } from '../data/MatchupEntry';
  import WaitingForGame from './WaitingForGame.svelte';
  import Bar from './Bar.svelte';

  interface KODatum {
    displayLabel: string;
    label: string;
    value: number;
  }

  interface PlayerStats {
    character?: string;
    percent?: number;
  }

  const MY_CONNECT_CODE: string = "BLAN#461"

  function stageInitialsToName(initials: string): string {
    const stageNames: { [key: string]: string } = {
      DL: "Dream Land N64",
      YS: "Yoshi's Story",
      PS: "Pokémon Stadium",
      FD: "Final Destination",
      FoD: "Fountain of Dreams",
      BF: "Battlefield"
    };

    return stageNames[initials] || "Battlefield";
  }

  function isCurrentStage(matchupEntry) {
    const fullStageName = stageInitialsToName(matchupEntry.stage);
    console.log("testing matchup.stage", matchupEntry.stage);
    console.log("settings.stageName", settings.stageName);
    return fullStageName === settings.stageName;
  }
 
  let matchupData: MatchupEntry[] | undefined = $state(); 
  let characterOne;
  let characterTwo;

  let currentPercent: number = $state(0);
  let displayStageName: string = $state("on Earth");

  let red: number = $state(255);
  let green: number = $state();
  let blue: number = $state();
  let color: string;
  let bars: Array<{ width: string; class: string }>;
  let opponentChar:string = $state("Test")
  let opponentPlayerIdx: number = 1;

  const socket: Socket = io("http://localhost:8090");

  onMount(() => {
    // Add typing to this 
    socket.on('game_start', async (settings: any) => {
      // Get Game Info
      displayStageName = settings.stageName;
      let players: any = settings.players;

      // If player length is not two show error
      if(players.length != 2) {
        console.error("Player length is not 2")
      }

      // Test Output
      console.log("players[0].characterShortName", players[0].characterShortName)
      console.log("players[1].characterShortName", players[1].characterShortName)

      // Get your and opponents index and character based on connect code
       let myPlayerIdx = players.findIndex((p: any) => p?.connectCode === MY_CONNECT_CODE);
      opponentPlayerIdx = players.findIndex((p: any) => p?.connectCode !== MY_CONNECT_CODE);

      const myChar = players[myPlayerIdx]?.characterShortName.toLowerCase();
      opponentChar = players[opponentPlayerIdx]?.characterShortName.toLowerCase();

      // Testing...
      console.log("myPlayerIdx", myPlayerIdx)
      console.log("opponentPlayerIdx", opponentPlayerIdx)
      console.log("myPlayer", myPlayer)
      console.log("opponentPlayer", opponentPlayer)
      console.log(`My connect code: ${myPlayer?.connectCode}`); 
      console.log(`Opponent's connect code: ${opponentPlayer?.connectCode}`); 

      // Load matchup data
      try {
        const matchupPath = `/data/${myChar}/vs_${opponentChar}.json`; 
        const response = await fetch(matchupPath);
        const allStagesKOData = await response.json();
        console.log("Loaded perspective matchup data:", allStagesKOData);
       const currentStageData = allStagesKOData.find(isCurrentStage);
        console.log("stageMatchupData", currentStageData);
        matchupData = currentStageData
      } catch (e) {
        console.error("Could not load matchup data for", matchupPath, e);
        matchupData = null;
      }
    });

    socket.on('slippi_update', (players: PlayerStats[]) => {
      console.log("WHOA received an event with info ", JSON.stringify(players));
      if (players && players[opponentPlayerIdx] && typeof players[opponentPlayerIdx].percent === 'number') {
        currentPercent = players[opponentPlayerIdx].percent;
        console.log("UPDATED currentPercent:", currentPercent);
      } else {
        console.log("No valid data received");
      }
    });

    return () => socket.disconnect();
  });

  const sampleDynamicData: any = {
    "fileDone?": false,
    "attacker": "Fox",
    "defender": "Marth",
    "stage": "YS",
    "moves": {
      "upSmash": 83.0,
      "strongUpTilt": 102.0,
      "downTilt": 145.0,
      "bAir": 124.0,
      "shuAir": 105.0
    }
  }

  const isHighlighted = (val: number): boolean =>
    typeof currentPercent === 'number' && currentPercent >= val;

  const calcWidth = (val: number): string =>
    val ? `${Math.min(100, (currentPercent / val) * 100).toFixed(1)}%` : '0%';

  // Compute how red text should look based on percent 
  
  run(() => {
    green = 255 - Math.round((currentPercent / 150) * 255);
  });
  run(() => {
    blue = 255 - Math.round((currentPercent / 150) * 255);
  });
  let percent_color = $derived(`rgb(${red},${green},${blue})`);

  let dynamicBars = $derived(Object.entries(matchupData?.moves || sampleDynamicData.moves).map(([moveName, koPercent]) => {
    //Need to sort by stage
    const highlighted = isHighlighted(koPercent);
    return {
      moveName: moveName,
      koPercent: koPercent,
      width: koPercent ? `${Math.min(100, (currentPercent / koPercent) * 100).toFixed(1)}%` : '0%',
      class: highlighted ? 'bar-fill' : 'bar-fill inactive'
    };
  }));

  console.log("dynamicBars", dynamicBars)
 </script>

<style>
  :global(body) {
    margin: 0;
    font-family: system-ui, sans-serif;
    background-color: #121212;
    color: #f0f0f0;
  }

  .dashboard {
    padding: 2rem;
  }

  .title {
    color: #ff3e00;
    font-size: 1.8rem;
    font-weight: bold;
    margin-bottom: 1.5rem;
  }

  .grid {
    display: grid;
    gap: 1rem;
    grid-template-columns: repeat(auto-fit, minmax(220px, 1fr));
  }

  .dev {
    padding: 1rem;
    outline: 2px ridge #ff3e00;
    border-radius: 1rem;
  }
  .title {
    display: flex;
    justify-content: space-between;
    color: #f0f0f0; 
  }
  .stage {
    color: #7C4DFF;
  }
</style>

<div class="dashboard">
  <div class="dev">
    <h4>Combat System Display Test</h4>
    <!-- For dev use -->
    <button onclick={() => currentPercent += 20}>+20%</button>
    <button onclick={() => currentPercent = 0}>Reset</button>
    <!-- -->
  </div>
  <div class="title">
    <h3>
      {opponentChar}'s current percent is <span style="color: {percent_color};">{currentPercent}%</span>
    </h3>
    <h3 class="stage">
      {displayStageName.length >0 ? `${displayStageName}` : ``}
    </h3>
  </div>

  <h1 class="title">KO Moves</h1>
  <div class="grid">
    {#each dynamicBars as bar}
      <Bar {...bar} />
    {/each}
  </div>
</div>
